[data-theme-mode="dark"][data-menu-styles="color"][data-vertical-style="doublemenu"] {
  .app-sidebar .main-sidebar-header .header-logo .toggle-white {
    display: block !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-dark {
    display: none !important;
  }
}

[data-theme-mode="dark"][data-menu-styles="gradient"][data-vertical-style="doublemenu"] {
  .app-sidebar .main-sidebar-header .header-logo .toggle-white {
    display: block !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-dark {
    display: none !important;
  }
}

[data-theme-mode="dark"][data-menu-styles="color"][data-nav-style="icon-click"] {
  .app-sidebar .main-sidebar-header .header-logo .toggle-white {
    display: block !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-dark {
    display: none !important;
  }
}

[data-theme-mode="dark"][data-menu-styles="gradient"][data-nav-style="icon-click"] {
  .app-sidebar .main-sidebar-header .header-logo .toggle-white {
    display: block !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-dark {
    display: none !important;
  }
}
@media (min-width: 992px) {
[data-nav-layout="horizontal"][data-theme-mode="dark"][data-menu-styles="light"]{
  .slide.has-sub .slide-menu.child1{
    border: 1px solid rgba(0,0,0,0.1);
  }

}
}

[data-theme-mode="light"][data-menu-styles="transparent"][data-nav-style="icon-hover"][data-toggled="icon-hover-closed"]{
  .app-sidebar .main-sidebar-header .header-logo  .desktop-logo{
    display: none !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-logo {
    display: block !important;
  }
}

[data-theme-mode="light"][data-menu-styles="transparent"][data-nav-style="icon-click"][data-toggled="icon-click-closed"]{
  .app-sidebar .main-sidebar-header .header-logo  .desktop-logo{
    display: none !important;
  }

  .app-sidebar .main-sidebar-header .header-logo .toggle-logo {
    display: block !important;
  }
}

[data-theme-mode="light"][data-default-header-styles="light"] .app-header .main-header-container .form-control,
[data-theme-mode="light"][data-header-styles="light"] .app-header .main-header-container .form-control {
  color: var(--default-text-color);
  box-shadow: none;
}
[dir="rtl"][data-vertical-style="doublemenu"] {
  .app-sidebar {
    .side-menu__item {
      padding: .7rem 0.5rem .5rem .75rem;
    }
  }
}

[data-width="boxed"] {
  .card-footer-boxed {
    padding: var(--bs-card-cap-padding-y) var(--bs-card-cap-padding-x) !important;
  }
}